@import url(https://fonts.googleapis.com/css?family=Lexend+Deca&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  line-height: 1.5;
  font-family: "Lexend Deca", sans-serif;
  background-color: #383838;
  font-size: 1.1rem;
}
a {
  text-decoration: none;
  color: black;
}
button {
  cursor: pointer;
}
.container {
  width: 60%;
  margin: auto;
}
.hover {
  transition: background-color 0.15s ease-in;
  -webkit-transition: background-color 0.15s ease-in;
  -moz-transition: background-color 0.15s ease-in;
  -ms-transition: background-color 0.15s ease-in;
  -o-transition: background-color 0.15s ease-in;
}
.hover:hover {
  background-color: #c4c4c4;
}

.answers {
  margin-top: 1rem;
  width: 100%;
  background-color: #383838;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 0.1rem;
  grid-auto-rows: 1fr;
  will-change: transform, opacity;
}
.answer-btn {
  padding: 1rem 0.5rem;
  background-color: whitesmoke;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.category-select {
  margin-top: 3rem;
}
.categories-loader {
  position: absolute;
  top: 40%;
}
.categories-loader svg {
  margin: auto;
  display: block;
}
.category-select h2 {
  margin-top: 2rem;
  text-align: center;
  color: white;
}
.categories {
  margin-top: 3rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 1fr;
  grid-gap: 0.5rem;
  align-items: stretch;
}
.category {
  padding: 1rem 2rem;
  background-color: whitesmoke;
  border: 1px solid rgba(25, 25, 112, 0.2);
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.header {
  padding: 0.5rem 0;
  color: whitesmoke;
  font-size: 1.7rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header a {
  font-weight: bold;
  color: whitesmoke;
  transition: color 0.15s ease-in;
  -webkit-transition: color 0.15s ease-in;
  -moz-transition: color 0.15s ease-in;
  -ms-transition: color 0.15s ease-in;
  -o-transition: color 0.15s ease-in;
}
.header a:hover {
  color: #c4c4c4;
}
.score {
  font-size: 1.4rem;
  display: flex;
}
.score h4 {
  margin-left: 0.5rem;
}

.question {
  min-height: 400px;
  margin-top: 4rem;
  padding: 1rem;
  padding-bottom: 0;
  background-color: whitesmoke;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  box-shadow: 3px 10px 25px -4px rgba(0, 0, 0, 0.68);
  text-align: center;
}
.question h3 {
  padding: 1rem 0;
  font-weight: 1;
}
.loader {
  margin: auto;
  align-self: center;
}
.next-btn {
  margin-bottom: 2rem;
  padding: 1rem 2rem;
  font-family: "Lexend Deca", sans-serif;
  outline: none;
  border: none;
  background-color: lightgray;
  color: black;
  transition: background-color 0.1s ease-in;
  -webkit-transition: background-color 0.1s ease-in;
  -moz-transition: background-color 0.1s ease-in;
  -ms-transition: background-color 0.1s ease-in;
  -o-transition: background-color 0.1s ease-in;
}
.next-btn:hover {
  background-color: #c4c4c4;
}

.result {
  margin-top: 0.5rem;
  min-width: 40%;
  text-align: center;
  margin-top: 1.5rem;
  justify-self: center;
}
.result div {
  width: 100%;
  height: 100%;
  background-color: whitesmoke;
  box-shadow: 0px 6px 28px -2px rgba(0, 0, 0, 0.3);
}

.result-btns {
  width: 40%;
  margin: auto;
  display: flex;
  justify-content: space-evenly;
}
.result-btns button {
  width: 40%;
  margin-top: 1rem;
  padding: 0.5rem 1rem;
}
.correct-answer {
  min-height: 30px;
  margin: 2rem;
}
.correct-answer p span {
  margin-left: 0.5rem;
  font-weight: bold;
}

@media only screen and (max-width: 1500px) {
  body {
    font-size: 1rem;
  }
  .container {
    width: 80%;
  }
}
@media only screen and (max-width: 1200px) {
  body {
    font-size: 0.9rem;
  }
  .categories {
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 0.25rem;
    margin-top: 1rem;
  }
  .category {
    padding: 0.5rem 1rem;
  }
  .next-btn {
    padding: 0.5rem 1rem;
  }
}

@media only screen and (max-width: 650px) {
  .container {
    width: 90%;
  }
  .header {
    font-size: 1.3rem;
  }
  .score {
    font-size: 1.1rem;
  }

  .category-select h2 {
    margin-top: 0;
  }
  .categories {
    grid-template-columns: repeat(2, 1fr);

    margin-top: 1;
  }
  .category {
    padding: 0.15rem 0.3rem;
  }
  .question {
    margin-top: 1rem;
    min-height: 250px;
  }

  .answers {
    padding: 0;
    grid-gap: 0.1rem;
  }
}

@media only screen and (max-width: 450px) {
  body {
    font-size: 0.7rem;
  }
  .answers {
    grid-template-columns: 1fr;
  }
  .answer-btn {
    padding: 0.7rem;
  }
  .result {
    width: 100%;
  }
  .next-btn {
    font-size: 0.7rem;
  }
}



